/* 앱 레이아웃 스타일 */
:host {
  display: block;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.app-container {
  min-height: 100vh;
  width: 100%;
}

.app-main {
  width: 100%;
  min-height: 100vh;
}

/* 탭바 */
app-tab-bar {
  height: 38px;
  flex-shrink: 0;
}

/* 상단 메뉴바 */
app-top-menu-bar {
  height: 48px;
  flex-shrink: 0;
}

/* 헤더 */
app-header {
  height: 44px;
  flex-shrink: 0;
}

.main-container {
  display: flex;
  flex: 1;
  position: relative;
  min-height: 0;
}

/* 페이지 재배열 컴포넌트 전체 화면 스타일 */
app-page-reorder {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

/* 애니메이션 */
.sidebar, .properties-panel {
  transition: transform 0.25s cubic-bezier(.4, 1.4, .6, 1), opacity 0.25s cubic-bezier(.4, 1.4, .6, 1);
}

.sidebar {
  transform: translateX(0);
  opacity: 1;
}

.sidebar.closed {
  transform: translateX(-100%);
  opacity: 0;
  pointer-events: none;
}

.properties-panel {
  transform: translateX(0);
  opacity: 1;
}

.properties-panel.closed {
  transform: translateX(100%);
  opacity: 0;
  pointer-events: none;
}

/* 텍스트 에디터 오버레이 */
.text-editors-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1000;
}

.text-editors-overlay app-text-editor {
  pointer-events: auto;
  position: absolute;
}

/* 드래그 앤 드롭 스타일 */
.file-select-guide.drag-over {
  border: 3px dashed #007acc;
  background: rgba(0, 122, 204, 0.05);
  position: relative;
}

.drag-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 122, 204, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 12px;
  z-index: 1000;
  pointer-events: none;
}

.drag-message {
  text-align: center;
  color: #007acc;
  pointer-events: none;
}

.drag-message svg {
  margin-bottom: 16px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.drag-message p {
  font-size: 18px;
  font-weight: 600;
  margin: 0;
}

/* 확인 모달 스타일 */
.confirm-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
}

.confirm-modal-content {
  background: var(--bg-primary);
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  min-width: 300px;
  max-width: 500px;
  margin: 20px;
}

.confirm-modal-header {
  padding: 20px 20px 10px 20px;
  
  h3 {
    margin: 0;
    font-size: 18px;
    font-weight: 600;
    color: #333;
  }
}

.confirm-modal-body {
  padding: 10px 20px;
  
  p {
    margin: 0;
    font-size: 14px;
    line-height: 1.5;
    color: #666;
  }
}

.confirm-modal-footer {
  padding: 15px 20px 20px 20px;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.confirm-modal-btn {
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  
  &.cancel {
    background: #6c757d;
    color: white;
    
    &:hover {
      background: #5a6268;
    }
  }
  
  &.confirm {
    background: #dc3545;
    color: white;
    
    &:hover {
      background: #c82333;
    }
  }
}

/* 뷰어 컨테이너 - 펜 그리기를 위한 상대적 위치 설정 */
.viewer-container {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}